import { Component, OnInit } from '@angular/core';
import { WheatherService } from './wheather.service';
import { Http } from '@angular/http';
@Component({
  selector: 'app-wheather',
  templateUrl: './wheather.component.html',
})
export class WheatherComponent implements OnInit {
	cityName = '' ;
	temp :number;
	isLoading = false;
	constructor(private wheatherService : WheatherService) { }
	ngOnInit() {
	
	}
	renderTemp(){
		this.isLoading = true;
		this.wheatherService.getTemp(this.cityName)
		.then(temp => {
			
						this.temp = temp;
						console.log(this.temp);
						this.isLoading = false
					})
		//	if(isNaN(this.temp)){
			//	alert('The city name ' + this.cityName + ' is not found !')
			//}
			
		
		.catch(err => {
			alert('City name not found');
			this.isLoading = false;
			this.cityName = '';
			console.log(err)
					
		});
		//console.log(this.wheatherService.url);
		//console.log(this.cityName);
		//console.log(this.temp);
		///console.log(typeof this.temp);
		//console.log(typeof this.temp === 'undefined');
		//console.log(!isNaN(this.temp));
		//if(typeof this.temp === 'undefined'){
		//	
		//}
	};
	
	getMessage(){
		if(this.isLoading){
			return 'Loading...';
		}
		return this.cityName === '' ? 'Enter your city name' : (this.cityName + ' has temp ' + this.temp);
	}

}
