import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {
	isShowForm = false;
	isShowFormUpdate = false;
	newEn = '';
	newVn = '';
	filterStatus = 'Xem_tat_ca'
	currentIndex= 0;
	arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
	];
	
	addWord(){
		this.arrWords.unshift({
			id: this.arrWords.length + 1,
			en: this.newEn,
			vn: this.newVn,
			memorized:false
		});
		this.newEn = '';
		this.newVn = '';
		alert('Them thanh cong');	
		this.isShowForm = false;
		
	}
	delWord(id){
		const index = this.arrWords.findIndex(word => word.id === id);
		alert('Xoa thanh cong');
		this.arrWords.splice(index,1);
	}
	editWord(id){
		const index = this.arrWords.findIndex(word => word.id === id);
		this.newEn = this.arrWords[index].en;
		this.newVn = this.arrWords[index].vn;
		this.currentIndex = index;
		this.isShowFormUpdate = true;
	}
	updateWord(){
		this.arrWords[this.currentIndex].en = this.newEn;
		this.arrWords[this.currentIndex].vn = this.newVn;
		alert('Cap nhat thanh cong');
		this.isShowFormUpdate = false;
	}
	checkFilterStatus(memorized){
		const xemtatca = this.filterStatus === 'Xem_tat_ca';
		const xemdanho = this.filterStatus === 'Xem_da_nho' && memorized;
		const xemchuanho = this.filterStatus === 'Xem_chua_nho' && !memorized;
		return xemchuanho || xemdanho || xemtatca;
	}
	constructor() { }

	ngOnInit(){
	}

}
